<template>
  <div class="Account-list">
    <div class="table">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span>账号列表</span>
        </div>
        <div class="text item">
          <el-table
            ref="multipleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 97%; margin:0 auto;"
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column label="账号" width="200">
              <template slot-scope="scope">{{ scope.row.account }}</template>
            </el-table-column>
            <el-table-column prop="userGroup" label="用户组" width="230"></el-table-column>
            <el-table-column prop="ctime" label="创建时间" show-overflow-tooltip></el-table-column>
            
            <el-table-column  label="个人头像">
              <template slot-scope="scope">
               <img :src="scope.row.imgUrl" alt="" width="40">
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button size="mini" @click="handleEdit(scope.row)">修改</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.row)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="page"
              :page-sizes="[5,10,15,30]"
              :page-size="pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>

          <div class="button">
            <el-button @click="delall" type="danger">批量删除</el-button>
            <el-button @click="cancel" type="primary">取消选择</el-button>
          </div>
        </div>
      </el-card>

      <el-card class="box-card" style="width:49%; margin-top:30px; float:left;">
        <div slot="header" class="clearfix"  >
          <span>总人数</span>
        </div>
        <div class="text item" style="margin-top:10px">
          <div id="totalnum" style="width:100%; height:470px;"></div>
        </div>
      </el-card>
      <el-card class="box-card" style="width:49%; margin-top:30px; float:right;">
        <div slot="header" class="clearfix">
          <span>管理用户</span>
        </div>
        <div class="text item"  style="margin-top:10px; padding-left:10px;">
          <div id="manage" style="width:100%; height:470px;"></div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import echarts from "echarts";
export default {
  data() {
    return {
      tableData: [],
      multipleSelection: [],
      page: 1,
      total: 0,
      pageSize: 5,
     imgUrl:'',
    };
  },

  mounted() {
    this.getuser();
    this.totalnum();
    this.manage();
  },
  methods: {
    // 批量删除
    delall() {
      let ids = [];
      this.multipleSelection.forEach(item => {
        ids.push(item.id);
      });
      this.deldata(ids.join(","));
    },
    cancel() {
      this.$refs.multipleTable.clearSelection();
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleEdit(row) {
      this.$router.push(`/index/update-account/${row.id}`);
    },
    handleDelete(row) {
      // console.log(row.id);
      this.deldata(row.id);
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.getuser();
      // console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getuser();
      // console.log(`当前页: ${val}`);
    },
    // 账户列表
    getuser() {
      axios
        .get(
          `http://127.0.0.1:3333/users.php?a=list&currentPage=${this.page}&pageSize=${this.pageSize}`
        )
        .then(data => {
          // console.log(data.data.data)
          if (data.data.code == 0) {
            this.tableData = data.data.data;
            this.imgUrl=data.data.data.imgUrl
            this.total = parseInt(data.data.total);
          }
        });
    },
    // 删除数据
    deldata(id) {
      if (confirm("确定删除这条数据吗？")) {
        axios
          .get(`http://127.0.0.1:3333/users.php?a=delete&id=${id}`)
          .then(data => {
            // console.log(data);
            if (data.data.code == 0) {
              this.$message({
                message: data.data.msg,
                type: "success"
              });
              this.getuser();
            } else {
              this.$message.error(data.data.msg);
            }
          });
      }
    },
   
    totalnum() {
      var myChart = echarts.init(document.getElementById("totalnum"));
      axios.get("http://127.0.0.1:3333/counter.php?a=users").then(data => {
        // console.log(data);
        if (data.data.code == 0) {
          let num = parseInt(data.data.data[0].num);
          num += parseInt(data.data.data[1].num);
          num += parseInt(data.data.data[2].num);
          let totalnums = num;
          myChart.setOption({
            tooltip: {
              trigger: "item",
              formatter: "{a} <br/>{b}: {c} ({d}%)"
            },
            legend: {
              orient: "vertical",
              left: 10,
              data: ["总人数"]
            },
            series: [
              {
                name: "总人数",
                type: "pie",
                radius: ["50%", "70%"],
                avoidLabelOverlap: false,
                label: {
                  normal: {
                    show: false,
                    position: "center"
                  },
                  emphasis: {
                    show: true,
                    textStyle: {
                      fontSize: "30",
                      fontWeight: "bold"
                    }
                  }
                },
                labelLine: {
                  normal: {
                    show: false
                  }
                },
                data: [{ value:totalnums, name: "总人数" }]
              }
            ]
          });
        }
      });
    },
    manage() {
      var myChart = echarts.init(document.getElementById("manage"));
      axios.get("http://127.0.0.1:3333/counter.php?a=users").then(data => {
        // console.log(data);
        if (data.data.code == 0) {
         let c = data.data.data[1].num;
         let b = data.data.data[2].num;
          let p = data.data.data[0].num;
          myChart.setOption({
            title: {
              text: "后台系统用户数据",
              left: "center"
            },
            tooltip: {
              trigger: "item",
              formatter: "{a} <br/>{b} : {c} ({d}%)"
            },
            legend: {
              orient: "vertical",
              left: "left",
              data: ["超级管理员", "部门管理员", "普通管理员"]
            },
            series: [
              {
                name: "访问来源",
                type: "pie",
                radius: "55%",
                center: ["50%", "60%"],
                data: [
                  { value:c, name: "超级管理员" },
                  { value:b, name: "部门管理员" },
                  { value:p, name: "普通管理员" }
                ],
                emphasis: {
                  itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: "rgba(0, 0, 0, 0.5)"
                  }
                }
              }
            ]
          });
        }
      });
    }
  }
};
</script>

<style >
.Account-list .button {
  border: 0;
  flex-direction: row;
  justify-content: flex-start;
  padding-bottom: 20px;
  margin-top: 30px;
}
.Account-list .button .el-button--danger {
  margin-left: 44px;
}
.Account-list .block {
  border: 0;
}
.Account-list .block .el-pagination {
  margin: 23px 0 0 40px;
}
.Account-list .table {
  width: 100%;
  background: #ffffff;
  margin: 0 auto;
}
</style>